#wrapper {
	max-width: 100%;
	overflow: hidden;
	position: relative;
}


.container{
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
  max-width: 1220px;
  &.container-big{
    max-width: 1440px;
  }
}

.full-section{
  margin: 0 -20px;
  @include media(">=widescreen") {
    margin: 0;
    width: 100vw;
    position: relative;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}

.title-global-section{
  font-size: 40px;
  font-weight: 600;
  line-height: 1.193;
  @include media(">=widescreen") {
    font-size: 56px;
  }
  &.small-title{
    @include media(">=tablet") {
      font-size: 32px;
    }
    @include media(">=widescreen") {
      font-size: 52px;
    }
  }
  &.big-title{
    font-size: 44px;
    font-weight: 400;
    line-height: 1.3;
    @include media(">=widescreen") {
        font-size: 64px;
    }
  }
  &.mb-0{
    margin-bottom: 0;
  }
  strong{
    font-weight: 900;
  }
}

.tooltip{
  display: inline-block;
  vertical-align: top;
  cursor: pointer;
  width: 14px;
  height: 14px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
  background-image: url("../images/icon-tooltip.svg");
}

.hover-tooltip{
  @include media("<tablet") {
    left: 20px !important;
    right: 20px !important;
    width: calc(100% - 40px) !important;
  }
}

.tooltip-text{
  background: $white;
  border: 1px solid $light-gray;
  padding: 10px 15px;
  border-radius: 2px;
  font-size: 12px;
  line-height: 1.2;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
  @include media(">=tablet") {
    width: 100%;
    max-width: 350px;
    text-align: center;
  }
}

.only-section{
  margin-bottom: 80px;
  @include media(">=widescreen") {
    margin-bottom: 137px;
  }
}

.slick-arrow{
  border: none;
  background: transparent;
  padding: 0;
  font-size: 16px;
  font-weight: 600;
  color: $black;
  line-height: 1.1875;
  outline: none;
  &.slick-disabled{
    opacity: 0.4;
  }
  &.slick-prev{
    &:before{
      content: '';
      width: 8px;
      height: 8px;
      background: currentColor;
      border-radius: 50%;
      display: inline-block;
      vertical-align: top;
      margin-right: 10px;
      margin-top: 5px;
    }
  }
  &.slick-next{
    &:after{
      content: '';
      width: 8px;
      height: 8px;
      background: currentColor;
      border-radius: 50%;
      display: inline-block;
      vertical-align: top;
      margin-left: 10px;
      margin-top: 5px;
    }
  }
}


.fancybox-slide--html{
  padding: 40px 6px;

}
.fancybox-container{
  .fancybox-navigation, .fancybox-infobar{
    display: none !important;
  }
}
.fancybox-is-open .fancybox-bg{
  opacity: 1;
}
.fancybox-bg{
  background: #EEEBE5;
}
.popup-content{
  padding: 70px 20px;
  position: relative;
  width: 90%;
  @include media(">=tablet") {
    padding: 115px 40px;
  }
  @include media(">=widescreen") {
    width: 100%;
    max-width: 1180px;
  }
  &.popup-copywriting{
    padding: 0;
    background: transparent;
  }
  &.popup-project{
    padding: 33px 20px 70px;
    @include media(">=tablet") {
      padding: 33px 40px 115px;
    }
    .fancybox-close-small{
      @include media(">=tablet") {
        top: 10px;
        right: 10px;
      }
    }
  }
  .fancybox-close-small{
    position: fixed;
    top: 5px;
    right: 5px;
    padding: 5px;
    @include media(">=tablet") {
      top: 20px;
      right: 20px;
    }
    @include media(">=widescreen") {
      top: 48px;
      right: 60px;
      padding: 0;
    }
  }
  .preview-video{
    .video-holder{
      position: relative;
      padding-top: 65.25%;
      width: 100%;
    }
    iframe{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  .preview-image, .preview-video{
    width: 100%;
    max-width: 770px;
    margin: 0 auto;
    text-align: center;
    line-height: 0;
    @include media(">=widescreen") {
      margin: -80px auto;
    }
  }
  .preview-copywriting{
    width: 100%;
    text-align: center;
    line-height: 0;
    position: relative;
    img{
      width: 100%;
      border-bottom: 10px solid #EEEBE5;
      &:last-child{
        border-bottom: none;
      }
      &.hide{
        @include media(">=widescreen") {
          display: none;
        }
      }
    }
    .btn{
      display: none;
      @include media(">=widescreen") {
        display: inline-block;
        bottom: 40px;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }

  .popup-audio-holder{
    width: 100%;
    max-width: 390px;
    margin: 0 auto;
    text-align: center;
    line-height: 0;
  }
  .popup-link{
    font-size: 16px;
    font-weight: 600;
    line-height: 1.13;
    position: absolute;
    top: 20px;
    right: 20px;
    text-decoration: none;
    color: black;
    max-width: 45%;
    @include media(">=tablet") {
      font-size: 24px;
      top: 40px;
      right: 30px;
    }
    @include media(">=widescreen") {
      right: 60px;
    }
    &:hover{
      color: $green;
    }
  }
  .title-block{
    font-size: 16px;
    font-weight: 600;
    line-height: 1.13;
    position: absolute;
    top: 20px;
    left: 20px;
    max-width: 45%;
    @include media(">=tablet") {
      font-size: 24px;
      top: 40px;
      left: 30px;
    }
    @include media(">=widescreen") {
      left: 60px;
    }
  }
  .caption-block{
    font-size: 12px;
    font-weight: 400;
    position: absolute;
    bottom: 12px;
    right: 12px;
    color: $black;
    @include media(">=tablet") {
      bottom: 37px;
      right: 43px;
    }
    &:before{
      content: '';
      display: inline-block;
      vertical-align: middle;
      width: 4px;
      height: 4px;
      background: currentColor;
      border-radius: 50%;
      margin-right: 8px;
      margin-top: -2px;
    }
    strong{
      font-weight: 600;
    }
  }
}

.js-tab-hidden {
  display: block !important;
  left: -9999px !important;
  position: absolute !important;
  top: -9999px !important;
}

img.grayscale {
  filter: url("data:image/svg+xml;utf8,<svg xmlns=\'http://www.w3.org/2000/svg\'><filter id=\'grayscale\'><feColorMatrix type=\'matrix\' values=\'0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0\'/></filter></svg>#grayscale");
  filter: gray;
  -webkit-filter: grayscale(100%);
}
.bg-green{
  background: $green;
}
.bg-red{
  background: $red;
}
.bg-gray{
  background: rgba($gray, 0.4);
}
.text-green{
  color: $green;
}
.text-red{
  color: $red;
}
.text-blue{
  color: #4267b2;
}
.font-weight-bold{
  font-weight: 700;
}
.mb-0{
  margin-bottom: 0;
}