/*
 * CSS Styles that are needed by jScrollPane for it to operate correctly.
 *
 * Include this stylesheet in your site or copy and paste the styles below into your stylesheet - jScrollPane
 * may not operate correctly without them.
 */

.jspContainer
{
  overflow: hidden;
  position: relative;
}

.jspPane
{
  position: absolute;
}

.jspVerticalBar
{
  background: #BFBFC9;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
  width: 16px;
}

.jspHorizontalBar
{
  background: #BFBFC9;
  bottom: 6px;
  height: 1px;
  left: 0;
  position: absolute;
  width: 100%;
}

.jspCap
{
  display: none;
}

.jspHorizontalBar .jspCap
{
  float: left;
}

.jspTrack
{
  background: #BFBFC9;
  position: relative;
}

.jspDrag
{
  background: #BFBFC9;
  cursor: pointer;
  left: 0;
  position: relative;
  top: 0;
  &:after{
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 12px;
    height: 12px;
    background: #151614;
    border-radius: 50%;
  }
}

.jspHorizontalBar .jspTrack,
.jspHorizontalBar .jspDrag
{
  float: left;
  height: 100%;
}

.jspArrow
{
  background: #50506d;
  cursor: pointer;
  direction: ltr;
  display: block;
  margin: 0;
  padding: 0;
  text-indent: -20000px;
}

.jspDisabled
{
  background: #80808d;
  cursor: default;
}

.jspVerticalBar .jspArrow
{
  height: 16px;
}

.jspHorizontalBar .jspArrow
{
  float: left;
  height: 100%;
  width: 16px;
}

.jspVerticalBar .jspArrow:focus
{
  border: 1px solid red;
  outline: none;
}

.jspCorner
{
  background: #eeeef4;
  float: left;
  height: 100%;
}

/* Yuk! CSS Hack for IE6 3 pixel bug :( */
* html .jspCorner
{
  margin: 0 -3px 0 0;
}