.logo-banner-section{
  padding: 12px 0 12px;
  position: relative;
  margin-bottom: 80px;
  @include media(">=tablet") {
    padding: 60px 0 55px;
    margin-bottom: 140px;
  }
  @include media(">=widescreen") {
    margin-bottom: 203px;
  }
  &:after{
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 120vw;
    background: $bg-gray;
    opacity: 0.4;
    z-index: -1;
  }
  &.custom-section{
    margin-bottom: 50px;
    @include media(">=tablet") {
      padding: 27px 0 22px;
      margin-bottom: 150px;
    }
    .logo-list{
      padding: 0 20px;
      .logo-item{
        margin-right: 30px;
        @include media(">=widescreen") {
            max-width: 205px;
        }
        &:last-child{
          margin-right: 0;
        }
      }
    }
  }
  .logo-list{
    list-style-type: none;
    padding: 20px 20px 20px 0;
    margin: 0 -20px 0 0;
    display: flex;
    align-items: center;
    width: calc(100% + 20px);
    overflow-x: auto;
    overflow-y: visible;
    @include media(">=widescreen") {
      justify-content: space-between;
    }
    .logo-item{
      margin-right: 60px;
      min-width: 80px;
      &:last-child{
        margin-right: 0;
        padding-right: 20px;
        @include media("<tablet") {
          min-width: 100px;
        }
        @include media("<desktop") {
          min-width: 140px;
        }
        @include media(">=desktop") {
          padding-right: 0;
        }
      }
      &.logo-item-big{
        @include media(">=tablet") {
          min-width: 140px;
        }
      }
    }
    a{
      display: block;
      line-height: 0;
      opacity: 1;
      transition: 0.3s;
      &:hover{
        opacity: 0.5;
      }
    }
  }
  .reviews-logo-list{
    list-style-type: none;
    padding: 15px 0;
    margin: 0 0 -30px;
    text-align: center;
    line-height: 1;
    @include media(">=tablet") {
      padding: 0;
      display: flex;
      align-items: center;
      justify-content: space-around;
      margin: 0;
    }
    .logo-item{
      padding: 0 10px;
      margin-bottom: 30px;
      @include media(">=tablet") {
        margin-bottom: 10px;
      }
    }
    .icon-block{
      margin-bottom: 10px;
    }
    .name-block{
      font-size: 22px;
      font-weight: 400;
      margin-bottom: 15px;
      strong{
        font-weight: 600;
      }
    }
    .star{
      width: 21px;
      height: 21px;
      display: inline-block;
      vertical-align: middle;
      background-size: contain;
      background-repeat: no-repeat;
      background-position: 50% 50%;
      &.star-blue{
        background-image: url("../images/star-blue.png");
      }
      &.star-red{
        background-image: url("../images/star-red.png");
      }
      &.star-green{
        background-image: url("../images/star-green.png");
      }
    }
  }
}

.logo-section{
  margin-bottom: 80px;
  @include media(">=widescreen") {
    margin-bottom: 151px;
  }
  .title-global-section{
    margin-bottom: 40px;
    @include media(">=widescreen") {
      margin-bottom: 80px;
    }
  }
  .logo-list{
    padding: 12px 0 12px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    list-style-type: none;
    margin: 0 -20px;
    position: relative;
    @include media(">=tablet") {
      padding: 62px 0 20px;
      margin: 0 -20px;
    }
    &:after{
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 120vw;
      background: $bg-gray;
      opacity: 0.4;
      z-index: -1;
    }
    .logo-item{
      padding: 0 20px;
      margin-bottom: 30px;
      text-align: center;
      @include media(">=tablet") {
        margin-bottom: 53px;
      }
      @include media("<tablet") {
          max-width: 50% !important;
      }
    }
  }
}

